
body {
  height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-color:hsl(190, 6%, 7%);
}


.name{
  margin-left: 1rem;
  color: hsl(210, 54%, 49%);
}

.nam{
  font-size: 1.8rem;
  color: white;
}

.navbar{
  margin-top: -3rem;
  margin-left: 1rem;
  position: relative;
  top:.3rem;
  
}

.nam{
  font-size: 1.6rem;
  font-family: sans-serf;
  margin-left: 1.8rem;
}
.veri{
    font-size: 1rem;
  color:hsl(190, 30%, 90%);
  margin-left: 1.1rem;
  margin-top: 1.2rem;
  margin-bottom: -3.5rem;
  font-family: 'shadows into light';
  position: relative;
  top:1.2rem
}
  .mobilenav{
    background-color: hsl(62, 3%, 30%);
    padding: .8rem;
    height: 5rem;
    font-size: 3rem;
    display: block;
    border-radius: 5px;  
  }
  .yes{
    transition: all ease-in-out .9s;
    height: 12rem;
    display: block;
  }
  .logos{
    background: hsl(0, 0%, 80%);
    border-radius: 2rem;
    margin-top: .3rem;
    margin-left: 3.5rem;
  }

  .nav{
    display: flex;
    justify-content: space-between;
  }
  .navbar{
    margin-top: .9rem;
  }
  .mubible{
    display: none;
    position: relative;
  }

  .flex{
    display: flex;
    justify-content: space-around;
    font-size: 1.4rem;
    color: hsl(190, 17%, 15%);
  }
  .flax:hover{
    color: white;
  }
  .flax{
    display: flex;
    margin-left: -1rem;
    p{
      margin-left: .7em;
      font-size: 1.1rem;
    }
  }
  .img{
    background: hsl(0, 0%, 80%);
    border-radius: 2rem;
    margin-top: 1rem;
  }
  .more{
    display: none;
  }
  .show{
    visibility: hidden;
    opacity: 0;
    translate: 0 -8vh;
    transition-property:  opacity display overlay;
    transition-duration: 1s;
    transition-behavior: allow-discrete;   
  }

  .shw{
    visibility: visible;
    opacity: 1;
    translate: 0 0;
    @starting-style{
      opacity: 0;
      translate: 0 -8vh;
    }
  }

@media(min-width:50em) {
  .logos{
    display: none;
  }
  .veri{
    display: none;
  }
  .show{
    display: none;
  }
  .yes{
    height: 6rem;
  }
  .more{
    display: flex;
    gap: 3rem;
    margin-right: 1rem;
  }

}



